import Header from "./Header";
import React from "react";
import { Outlet } from "react-router-dom";
import Footer from "./Footer";
import './css/outlet.css';
function Layout() {
  return (
    <main>
      <Header />
      <div className="outlet">
        <Outlet />
      </div>      
      <Footer />
    </main>
  );
}

export default Layout;
